export default{
	data(){
		return{
			NOdata:true,//暂无数据
			userList: [],
			/* 窗口尺寸 */
			winSize: {},
			/* 显示遮罩 */
			showShade: false,
			/* 显示操作弹窗 */
			showPop: false,
			/* 弹窗按钮列表 */
			popButton: ["标为关注", "置顶聊天", "删除该聊天"],
			/* 弹窗定位样式 */
			popStyle: "",
			/* 选择的用户下标 */
			pickerUserIndex: -1
		}
	},
	onLoad() {
		this.getListData();
		this.getWindowSize();
		






	},
	methods: {
		/* 列表触摸事件 */
		listTap() {
			/* 因弹出遮罩问题，所以需要在遮罩弹出的情况下阻止列表事件的触发 */
			if (this.showShade) {
				return;
			}
			uni.navigateTo({
				url:'../../../ShopDetails/informtion/informtion'
			})
	
			console.log("列表触摸事件触发")
		},
		/* 获取列表数据 */
		getListData() {
			let list = [];
			for (let i = 0; i < 5; i++) {
				list.push({
					"name": `第${i+1}个用户`,
					"time": '5月20日',
					"info": `这是第${i+1}个用户的聊天信息`
				})
			}
			this.userList = list;
		},
		/* 获取窗口尺寸 */
		getWindowSize() {
			uni.getSystemInfo({
				success: (res) => {
					this.winSize = {
						"witdh": res.windowWidth,
						"height": res.windowHeight
					}
				}
			})
		},
		/* 长按监听 */
		onLongPress(e) {
			let [touches, style, index] = [e.touches[0], "", e.currentTarget.dataset.index];
	
			/* 因 非H5端不兼容 style 属性绑定 Object ，所以拼接字符 */
			if (touches.clientY > (this.winSize.height / 2)) {
				style = `bottom:${this.winSize.height-touches.clientY}px;`;
			} else {
				style = `top:${touches.clientY}px;`;
			}
			if (touches.clientX > (this.winSize.witdh / 2)) {
				style += `right:${this.winSize.witdh-touches.clientX}px`;
			} else {
				style += `left:${touches.clientX}px`;
			}
	
			this.popStyle = style;
			this.pickerUserIndex = Number(index);
			this.showShade = true;
			this.$nextTick(() => {
				setTimeout(() => {
					this.showPop = true;
				}, 10);
			});
		},
		/* 隐藏弹窗 */
		hidePop() {
			this.showPop = false;
			this.pickerUserIndex = -1;
			setTimeout(() => {
				this.showShade = false;
			}, 250);
		},
		/* 选择菜单 */
		pickerMenu(e) {
			let index = Number(e.currentTarget.dataset.index);
			console.log(`第${this.pickerUserIndex+1}个用户,第${index+1}个按钮`);
			// 在这里开启你的代码秀
	
			uni.showToast({
				title: `第${this.pickerUserIndex+1}个用户,第${index+1}个按钮`,
				icon: "none",
				mask: true,
				duration: 600
			});
	
			/* 
			 因为隐藏弹窗方法中会将当前选择的用户下标还原为-1,
			 如果行的菜单方法存在异步情况，请在隐藏之前将该值保存，或通过参数传入异步函数中
			 */
			this.hidePop();
		}
	}
}
